<template>
  <b-container fluid class="mt--7">
    <b-row>
      <b-col lg="2" style="padding: 0;">
        <form-loader height_custom="260px" v-if="loading" />

        <card v-else header-classes="bg-transparent">
          <b-row class="group-filter">
            <b-col cols="6" lg="6" style="display: flex;">
              <base-button @click="initFilters" size="sm" class="filter" type="default">{{ $t('general.filter') }}
              </base-button>
              <base-button @click="eraseFilters" size="sm" class="filter" type="default">{{ $t('general.clear') }}
              </base-button>
            </b-col>
          </b-row>
          <br><br>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.tables.index.number')"
                v-model="search_by_number" />
            </b-col>
          </b-row>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.tables.index.status')"
                v-model="search_by_status" />
            </b-col>
          </b-row>

          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input :name="$t('cruds.tables.index.area_id')" :label="$t('cruds.tables.index.area_id')">
                <el-select v-model="search_by_area_id" :placeholder="$t('cruds.tables.index.area_id')">
                  <el-option v-for="option in lists.areas" :key="option.label" :label="option.label"
                    :value="option.value">
                  </el-option>
                </el-select>
              </base-input>
            </b-col>
          </b-row>
        </card>
      </b-col>

      <b-col lg="10">
        <form-loader height_custom="410px" v-show="loading" />

        <card v-show="!loading">
          <b-col cols="12" lg="12" class="py-2 text-right" v-if="$can('table_create')">
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_export')">
              <i class="fa-solid fa-download"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_import')">
              <i class="fa-solid fa-upload"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.print')">
              <i class="fa-solid fa-print"></i>
            </base-button>
            <base-button @click="navigateToGrid" size="sm" class="btn btn-default" :title="$t('general.grid')">
              <i class="fa-solid fa-table-cells"></i>
            </base-button>
            <base-button @click="navigateToCreate" size="sm" class="btn btn-default">
              {{ $t('general.new') }}
            </base-button>
          </b-col>

          <sw-datatable :tableColumns="tableColumns" :tableData="data" :total="total" :loading="loading"
            @paginationChanged="clickPagination" @delete="deleteRow" :xprops="xprops" />

          <div class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap py-1">
            <p class="card-category">
              {{ total }} {{ $t('general.datatable_registers') }}
            </p>

            <base-pagination class="pagination-no-border py-0" :current="query.page" :per-page="query.limit"
              :total="total" @change="clickPagination($event)">
            </base-pagination>
          </div>
        </card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import tables_filter from './mixins/tables_filter'
import DatatableQRcodeImage from '@/components/Datatables/DatatableQRcodeImage.vue';
import DatatableLink from '@/components/Datatables/DatatableLink.vue';
import DatatableTableStatus from '@/components/Datatables/DatatableTableStatus.vue';
import DatatableRelationship from '@/components/Datatables/DatatableRelationship.vue';

export default {
  mixins: [tables_filter],
  data() {
    return {
      tableColumns: {
        'image_path': {
          prop: 'image_path',
          label: '',
          minWidth: 70,
          component: DatatableQRcodeImage
        },
        'table_link': {
          prop: 'table_link',
          label: this.$t('cruds.tables.index.table_link'),
          minWidth: 140,
          sortable: true,
          component: DatatableLink
        },
        'area.name': {
          prop: 'area.name',
          label: this.$t('cruds.tables.index.area_id'),
          minWidth: 140,
          sortable: true,
          component: DatatableRelationship
        },
        'status': {
          prop: 'status',
          label: this.$t('cruds.tables.index.status'),
          minWidth: 140,
          sortable: true,
          component: DatatableTableStatus
        },
      },
      xprops: {
        module: "TablesIndex",
        route: "tables",
        permission_prefix: "table_",
        view_action: {
          show: false,
          edit: true,
          delete: true,
        },
      },
    };
  },
  mounted() {
    this.query.limit = 8
    this.initFilters(this.query);
  },
  computed: {
    ...mapGetters("TableIndex", ["data", "total", "lists", "loading"]),
  },
  methods: {
    ...mapActions("TableIndex", [
      "fetchIndexData",
      "setQuery",
      "resetState",
      "setLinearFilters",
      "destroyData"
    ]),
    navigateToCreate() {
      this.$router.push({ name: "tables.create" });
    },
    navigateToGrid() {
      this.$setViewPath('tables', '/grid')
      this.$router.push({ name: "tables.grid" });
    },
    deleteRow(row) {
      this.destroyData(row.id)
    }
  },
};
</script>

<style scoped>
.filter {
  font-size: xx-small;
}

.group-filter {
  margin-left: -26px;
  margin-bottom: -20px;
}
</style>